import { useState, useEffect } from 'react';
import { Loader } from '../util/kakaoMapApiLoader.js';

/**
 * Kakao Map Api Loading .
 *
 *  hook cleanup  Kakao Map Api   ,  hook       Loading .
 *
 *   `loading` state hook      state .
 *
 * loading  `Map`  conditional rendering   .
 */
const useKakaoLoader = options => {
  const [state, setState] = useState([true, undefined]);
  useEffect(() => {
    new Loader({
      ...options
    }).load().then(() => setState([false, undefined])).catch(error => {
      setState([false, error]);
    });
  },
  // eslint-disable-next-line react-hooks/exhaustive-deps
  [JSON.stringify(options)]);
  return state;
};

export { useKakaoLoader };
